//vuex，提供全局数据存储
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        darkTheme: false, //主题
    },
    getters: {
        //主题
        getdarkTheme: state => {
            return state.darkTheme;
        },
    },
    //将vue组件中的数据赋值给state中的数据
    mutations: {
        //此函数默认传两个参数，第一个是state对象，第二个是我们填入的数据
        setdarkTheme: (state, status) => {
            state.darkTheme = status;
        },
    }
})


