/*
 *api接口统一管理
 */
import axios from './request.js'

const api = {
    //公共参数
    

    login(params) { //登录
        return axios.get('/login', {params})
    },
    //获取搞笑段子列视频表
    getJokeList(params) {
        return axios.get('https://api.apiopen.top/getJoke', { params })
    },
    //获取搞笑段子列表
    // http://hn216.api.yesapi.cn/?s=App.Common_Joke.RandOne&return_data=0&num=100&app_key=0D01C4CA26367BA195F8402DACC184C7&sign=CDF64287311F947362FC7C69AF1F9C21
    getJokeByTextList(params) {
        return axios.get('http://hn216.api.yesapi.cn/?s=App.Common_Joke.RandOne', { params })
    },
    //获取上传的图片列表
    // http://hn216.api.yesapi.cn/?s=App.CDN.RecordList&return_data=0&order_type=1&page=1&perpage=20&app_key=0D01C4CA26367BA195F8402DACC184C7&sign=CD07420B3A76D1718062F4F5F94C753E
    getUploadRecordListList(params) {
        return axios.get('http://hn216.api.yesapi.cn/?s=App.CDN.RecordList', { params })
    },
    // 随机获取一张壁纸
    getBackgroudImg(params){
        return axios.get('http://api.wpbom.com/api/wallpa.php', { params })
    }
    
}

export default api



